"use client";

import React, { useEffect, useRef, useState } from "react";
import { Search as SearchIcon } from "lucide-react";

// â­ SOCKET IMPORT
import { connectSocket, disconnectSocket, getSocket } from "@/src/lib/socket";

const Search = ({ onSearch }) => {
  const [searchQuery, setSearchQuery] = useState("");

  const [notifications, setNotifications] = useState([]);
  const [unreadCount, setUnreadCount] = useState(0);
  const [open, setOpen] = useState(false);

  const dropdownRef = useRef(null);

  const BASE_URL = "https://matrimonial-backend-7ahc.onrender.com";

  /* --------------------------------------------
      1) ADMIN PREF â†’ CONNECT SOCKET
  -------------------------------------------- */
  const loadAdminPrefs = async () => {
    try {
      const token = localStorage.getItem("token");

      const res = await fetch(`${BASE_URL}/admin/profile`, {
        headers: { Authorization: `Bearer ${token}` },
      });

      const json = await res.json();

      if (json.success) {
        const admin = json.data;

        if (admin.notifications === true) {
          connectSocket(admin._id);
        } else {
          disconnectSocket();
        }
      }
    } catch (err) {
      console.log("Admin Pref error:", err);
    }
  };

  useEffect(() => {
    loadAdminPrefs();
  }, []);

  /* --------------------------------------------
      2) FETCH ALL NOTIFICATIONS
  -------------------------------------------- */
  const fetchNotifications = async () => {
    try {
      const token = localStorage.getItem("token");

      const res = await fetch(`${BASE_URL}/api/notification/me`, {
        headers: { Authorization: `Bearer ${token}` },
      });

      const data = await res.json();

      if (data.success) {
        const list = data.data.reverse();
        setNotifications(list);
        setUnreadCount(list.filter((n) => !n.read).length);
      }
    } catch (error) {
      console.log("Fetch Notification Error:", error);
    }
  };

  useEffect(() => {
    fetchNotifications();
  }, []);

  /* --------------------------------------------
      3) SOCKET SYNC LISTENERS (VERY IMPORTANT)
  -------------------------------------------- */
  useEffect(() => {
    const socket = getSocket();
    if (!socket) return;

    console.log("ðŸŸ¢ Realtime Listener Enabled");

    // NEW NOTIFICATION
    socket.on("new-notification", (data) => {
      setNotifications((prev) => [data, ...prev]);
      setUnreadCount((c) => c + 1);
    });

    // MARK ALL READ SYNC
    socket.on("all-read", () => {
      setNotifications((prev) => prev.map((n) => ({ ...n, read: true })));
      setUnreadCount(0);
    });

    // MARK ONE READ SYNC
    socket.on("one-read", ({ id }) => {
      setNotifications((prev) =>
        prev.map((n) => (n._id === id ? { ...n, read: true } : n))
      );
      setUnreadCount((c) => Math.max(c - 1, 0));
    });

    // DELETE ONE SYNC
    socket.on("delete-one", ({ id }) => {
      setNotifications((prev) => prev.filter((n) => n._id !== id));
    });

    // DELETE ALL SYNC
    socket.on("delete-all", () => {
      setNotifications([]);
      setUnreadCount(0);
    });

    return () => {
      socket.off("new-notification");
      socket.off("all-read");
      socket.off("one-read");
      socket.off("delete-one");
      socket.off("delete-all");
    };
  }, []);

  /* --------------------------------------------
      SEARCH
  -------------------------------------------- */
  const handleSearch = () => onSearch(searchQuery.toLowerCase());
  const handleKeyDown = (e) => e.key === "Enter" && handleSearch();

  /* --------------------------------------------
      CLOSE DROPDOWN OUTSIDE CLICK
  -------------------------------------------- */
  useEffect(() => {
    const handler = (e) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target)) {
        setOpen(false);
      }
    };
    document.addEventListener("mousedown", handler);
    return () => document.removeEventListener("mousedown", handler);
  }, []);

  /* --------------------------------------------
      MARK ALL READ (BELL CLICK)
  -------------------------------------------- */
  const handleBellClick = async () => {
    const show = !open;
    setOpen(show);

    if (show) {
      markAll(); // Auto mark all
    }
  };

  /* --------------------------------------------
      MARK ALL READ (WITH SOCKET EMIT)
  -------------------------------------------- */
  const markAll = async () => {
    const token = localStorage.getItem("token");

    // Frontend update
    setNotifications((p) => p.map((n) => ({ ...n, read: true })));
    setUnreadCount(0);

    // Backend update
    await fetch(`${BASE_URL}/api/notification/mark-all`, {
      method: "PATCH",
      headers: { Authorization: `Bearer ${token}` },
    });

    // Socket sync for everywhere
    getSocket()?.emit("all-read");
  };

  /* --------------------------------------------
      MARK ONE READ
  -------------------------------------------- */
  const markAsRead = async (id) => {
    const token = localStorage.getItem("token");

    // front update
    setNotifications((p) =>
      p.map((n) => (n._id === id ? { ...n, read: true } : n))
    );
    setUnreadCount((c) => Math.max(0, c - 1));

    // backend update
    await fetch(`${BASE_URL}/api/notification/mark-read/${id}`, {
      method: "PATCH",
      headers: { Authorization: `Bearer ${token}` },
    });

    // socket sync
    getSocket()?.emit("one-read", { id });
  };

  /* --------------------------------------------
      DELETE ONE
  -------------------------------------------- */
  const deleteOne = async (id) => {
    const token = localStorage.getItem("token");

    setNotifications((p) => p.filter((n) => n._id !== id));

    await fetch(`${BASE_URL}/api/notification/${id}`, {
      method: "DELETE",
      headers: { Authorization: `Bearer ${token}` },
    });

    getSocket()?.emit("delete-one", { id });
  };

  /* --------------------------------------------
      DELETE ALL
  -------------------------------------------- */
  const deleteAll = async () => {
    const token = localStorage.getItem("token");

    setNotifications([]);
    setUnreadCount(0);

    await fetch(`${BASE_URL}/api/notification/delete-all`, {
      method: "DELETE",
      headers: { Authorization: `Bearer ${token}` },
    });

    getSocket()?.emit("delete-all");
  };

  /* --------------------------------------------
      UI
  -------------------------------------------- */
  return (
    <>
      {/* FIXED NAVBAR WITH SCROLL CONTAINER */}
      <div className="w-full bg-[#F2F2F2] border-b-2 fixed top-0 left-0 right-0 z-50 overflow-x-auto">
        <div className="min-w-[1272px] flex justify-between items-center p-3 mx-auto">
          
          {/* TITLE - FIXED LEFT */}
          <div className="flex-shrink-0">
            <h1 className="text-xl text-black font-semibold whitespace-nowrap">
              Profile Details
            </h1>
          </div>

          {/* RIGHT SIDE - FLEXIBLE */}
          <div className="flex gap-5 items-center flex-shrink-0 ml-auto">
            
            {/* SEARCH - ORIGINAL SIZE */}
            <div className="flex items-center bg-white border border-gray-300 rounded-full px-4 py-2 w-[350px] shadow-sm flex-shrink-0">
              <SearchIcon 
                className="text-gray-600 cursor-pointer w-5 h-5" 
                onClick={handleSearch} 
              />
              <input
                type="text"
                placeholder="Search..."
                className="ml-2 w-full outline-none text-sm bg-transparent"
                value={searchQuery}
                onChange={(e) => {
                  setSearchQuery(e.target.value);
                  onSearch(e.target.value.toLowerCase());
                }}
                onKeyDown={handleKeyDown}
              />
            </div>

            {/* NOTIFICATION BELL */}
            <div className="relative flex-shrink-0" ref={dropdownRef}>
              <button
                onClick={handleBellClick}
                className="p-1 rounded-lg hover:bg-gray-200 transition-colors relative flex items-center justify-center"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="30"
                  height="30"
                  fill="#FFC107"
                  viewBox="0 0 24 24"
                >
                  <path d="M12 24c1.104 0 2-.897 2-2h-4c0 1.103.896 2 2 2zm6.707-5 1.293 1.293V21H4v-1.707L5.293 19H6v-7c0-3.309 2.691-6 6-6s6 2.691 6 6v7h.707z" />
                </svg>

                {unreadCount > 0 && (
                  <span className="absolute -top-1 -right-1 h-4 w-4 bg-red-600 text-white text-[10px] flex items-center justify-center rounded-full border-2 border-white">
                    {unreadCount}
                  </span>
                )}
              </button>

              {/* DROPDOWN */}
              {open && (
                <div className="absolute right-0 mt-3 w-[330px] bg-white shadow-lg border rounded-lg p-3 z-50">
                  <div className="flex justify-between mb-2">
                    <h2 className="font-semibold">Notifications</h2>
                    <button onClick={markAll} className="text-blue-600 text-sm">
                      Mark all read
                    </button>
                  </div>

                  {/* LIST */}
                  <div className="max-h-[300px] overflow-y-auto">
                    {notifications.length === 0 ? (
                      <p className="text-center py-3 text-gray-500">No notifications</p>
                    ) : (
                      notifications.map((n) => (
                        <div
                          key={n._id}
                          className={`p-3 border-b flex justify-between items-start ${
                            !n.read ? "bg-yellow-50" : ""
                          }`}
                        >
                          <div className="flex-1 min-w-0">
                            <h3 className="font-semibold truncate">{n.title}</h3>
                            <p className="text-sm text-gray-600 mt-1 truncate">{n.message}</p>
                            <p className="text-xs text-gray-400 mt-1">
                              {new Date(n.createdAt).toLocaleString()}
                            </p>

                            {!n.read && (
                              <button onClick={() => markAsRead(n._id)} className="text-blue-600 text-xs mt-1">
                                Mark as read
                              </button>
                            )}
                          </div>

                          <button onClick={() => deleteOne(n._id)} className="text-red-500 text-xs ml-2 flex-shrink-0">
                            Delete
                          </button>
                        </div>
                      ))
                    )}
                  </div>

                  {notifications.length > 0 && (
                    <button
                      onClick={deleteAll}
                      className="w-full mt-2 py-2 text-red-600 text-sm border-t"
                    >
                      Delete All
                    </button>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* SPACER FOR FIXED HEADER */}
      <div className="h-16"></div>
    </>
  );
};

export default Search;